export const activeNav = () => {
	const navLinks = document.querySelectorAll('.main-nav__link[href^="#"]');
	const sections = document.querySelectorAll('section[id]');

	if (!navLinks.length || !sections.length) return;

	const updateActiveNav = () => {
		const scrollPosition = window.scrollY + 150; // отступ для header

		let activeSection = null;

		// Проходим по всем секциям и находим активную
		sections.forEach(section => {
			const sectionTop = section.offsetTop;
			const sectionHeight = section.offsetHeight;

			if (scrollPosition >= sectionTop && scrollPosition < sectionTop + sectionHeight) {
				activeSection = section.id;
			}
		});

		// ВСЕГДА убираем активный класс у всех ссылок
		navLinks.forEach(link => {
			link.classList.remove('is-active');
			link.blur(); // снимаем фокус со всех ссылок
		});

		// Если нашли активную секцию, добавляем класс к соответствующей ссылке
		if (activeSection) {
			const activeLink = document.querySelector(`.main-nav__link[href="#${activeSection}"]`);
			if (activeLink) {
				activeLink.classList.add('is-active');
			}
		}
	};

	// Вызываем при скролле
	let scrollTimeout;
	window.addEventListener('scroll', () => {
		clearTimeout(scrollTimeout);
		scrollTimeout = setTimeout(updateActiveNav, 10);
	});

	// Вызываем сразу для определения начального состояния
	updateActiveNav();
};