import { openTelegram } from '../utils/telegram';

export class PromoForm {
	constructor(username = "feslevee26") {
		this.username = username;
		this.form = document.querySelector(".promo__form");
		if (this.form) {
			this.inputs = Array.from(this.form.querySelectorAll(".custom-input__field"));
			this.submitButton = this.form.querySelector(".promo__btn");
			this.handleSubmit = this.handleSubmit.bind(this);
			this.bindEvents();
		}
	}

	bindEvents() {
		this.form.addEventListener("submit", this.handleSubmit);
		if (this.submitButton) {
			this.submitButton.addEventListener("click", (e) => {
				// Fallback if something prevents submit
				if (this.form) {
					// Let the submit handler run
				}
			});
		}
		this.inputs.forEach((input) => {
			input.addEventListener("input", () => this.clearError(input));
		});
	}

	handleSubmit(event) {
		event.preventDefault();
		const { isValid, values, firstInvalid } = this.validate();
		if (!isValid) {
			if (firstInvalid) {
				firstInvalid.focus();
			}
			return;
		}

		const messageLines = [
			"Здравствуйте! Хочу пробную неделю!",
			values.name ? `Имя: ${values.name}` : "",
			values.phone ? `Телефон: ${values.phone}` : "",
		].filter(Boolean);

		const text = messageLines.join("\n");
		openTelegram(this.username, text);
	}

	validate() {
		let isValid = true;
		const nameInput = /** @type {HTMLInputElement|null} */ (this.form.querySelector('input[type="text"]'));
		const phoneInput = /** @type {HTMLInputElement|null} */ (this.form.querySelector('input[type="tel"]'));

		const values = {
			name: nameInput ? nameInput.value.trim() : "",
			phone: phoneInput ? phoneInput.value.trim() : "",
		};

		let firstInvalid = null;

		// Name: 2+ chars
		if (!values.name || values.name.length < 2) {
			isValid = false;
			this.setError(nameInput, "Введите имя (минимум 2 символа)");
			firstInvalid = firstInvalid || nameInput;
		}

		// Phone: digits length >= 10
		const digits = values.phone.replace(/\D/g, "");
		if (digits.length < 10) {
			isValid = false;
			this.setError(phoneInput, "Укажите корректный телефон (не менее 10 цифр)");
			firstInvalid = firstInvalid || phoneInput;
		}

		return { isValid, values, firstInvalid };
	}

	setError(input, message) {
		if (!input) {
			return;
		}
		const wrapper = input.closest(".custom-input");
		if (wrapper) {
			wrapper.classList.add("is-invalid");
			let errorEl = wrapper.querySelector(".custom-input__error");
			const errorId = `${input.name || input.type}-error`;
			if (!errorEl) {
				errorEl = document.createElement("div");
				errorEl.className = "custom-input__error";
				errorEl.id = errorId;
				wrapper.appendChild(errorEl);
			}
			errorEl.textContent = message;
			// ensure id stays in sync if element existed
			if (!errorEl.id) {
				errorEl.id = errorId;
			}
		}
		input.setAttribute("aria-invalid", "true");
		input.setAttribute("aria-describedby", `${input.name || input.type}-error`);
	}

	clearError(input) {
		const wrapper = input.closest(".custom-input");
		if (wrapper) {
			wrapper.classList.remove("is-invalid");
			const errorEl = wrapper.querySelector(".custom-input__error");
			if (errorEl) {
				errorEl.remove();
			}
		}
		input.removeAttribute("aria-invalid");
		input.removeAttribute("title");
		input.removeAttribute("aria-describedby");
	}
}
