export class Tickets {
	constructor() {
		this.init();
	}

	init() {
		const list = document.querySelector('.tickets__list');
		if (!list) {
			return;
		}

		const setCardsTabbable = () => {
			list.querySelectorAll('.ticket-card').forEach((card) => {
				if (!card.hasAttribute('tabindex')) {
					card.setAttribute('tabindex', '0');
				}
				card.setAttribute('role', 'button');
				card.setAttribute('aria-pressed', card.classList.contains('ticket-card--active') ? 'true' : 'false');
			});
		};

		const activateCard = (targetCard) => {
			const cards = list.querySelectorAll('.ticket-card');
			cards.forEach((c) => {
				c.classList.remove('ticket-card--active');
				c.setAttribute('aria-pressed', 'false');
			});
			targetCard.classList.add('ticket-card--active');
			targetCard.setAttribute('aria-pressed', 'true');
		};

		list.addEventListener('click', (event) => {
			const btn = event.target.closest('.ticket-card__btn');
			if (btn) {
				return; // не переключаем по клику на кнопку внутри карточки
			}
			const card = event.target.closest('.ticket-card');
			if (!card || !list.contains(card)) {
				return;
			}
			activateCard(card);
		});

		list.addEventListener('keydown', (event) => {
			if (event.key !== 'Enter' && event.key !== ' ') {
				return;
			}
			const card = event.target.closest('.ticket-card');
			if (!card || !list.contains(card)) {
				return;
			}
			event.preventDefault();
			activateCard(card);
		});

		setCardsTabbable();
	}
}


