import { iosVhFix } from './utils/ios-vh-fix';
import { initModals } from './modules/modals/init-modals';
import { Burger } from './components/burger';
import { Slider } from './components/slider';
import { Tickets } from './components/tickets';
import { smoothScroll } from './utils/smoothScroll';
import { activeNav } from './utils/active-nav';
import { PromoForm } from './components/promo-form';
import { bindTelegramLinks } from './utils/telegram';

// ---------------------------------


window.addEventListener('DOMContentLoaded', () => {
	// Utils
	iosVhFix();
	smoothScroll();
	activeNav();
	bindTelegramLinks('feslevee26');


	// Components
	new Burger(); // создаем экземпляр класса
	new Slider();
	new Tickets();
	new PromoForm('feslevee26');


	// Modules
	window.addEventListener('load', () => {
		initModals();
	});
});

