export class Slider {
	constructor() {
		this.init();
	}

	init() {
		// Единый инстанс Swiper с breakpoint'ами
		const swiper = new Swiper('.swiper', {
			centeredSlides: false,
			speed: 600,
			effect: 'slide',
			grabCursor: true,
			navigation: {
				nextEl: '.swiper__button_next',
				prevEl: '.swiper__button_prev',
				disabledClass: 'swiper-button-disabled',
				hiddenClass: 'swiper-button-hidden',
			},
			scrollbar: {
				el: '.swiper-scrollbar',
				draggable: true,
				dragSize: 100,
				hide: false,
				snapOnRelease: true,
				dragClass: 'swiper-scrollbar-drag',
				progressbarFillClass: 'swiper-scrollbar-fill',
			},
			breakpoints: {
				0: {
					slidesPerView: 'auto',
					spaceBetween: 12,
					centeredSlides: true,
					centeredSlidesBounds: true,
					loop: false,
				},
				768: {
					slidesPerView: 'auto',
					spaceBetween: 12,
					centeredSlides: false,
				},
				1200: {
					slidesPerView: 'auto',
					spaceBetween: 24,
				},
			},
		});
	}
}