export const openTelegram = (username, text) => {
	if (!username) {
		return;
	}
	const encodedText = encodeURIComponent(text || "");
	const deepLink = `tg://resolve?domain=${username}&text=${encodedText}`;
	const webLink = `https://t.me/${username}?text=${encodedText}`;

	let fallbackTimer = null;

	const clear = () => {
		if (fallbackTimer) {
			clearTimeout(fallbackTimer);
			fallbackTimer = null;
		}
		document.removeEventListener("visibilitychange", onVisibilityChange);
		window.removeEventListener("pagehide", onPageHide);
		window.removeEventListener("blur", onBlur);
	};

	const onVisibilityChange = () => {
		if (document.visibilityState === "hidden") {
			clear();
		}
	};
	const onPageHide = () => clear();
	const onBlur = () => clear();

	// Schedule fallback to web link in the same tab (less likely to be blocked)
	fallbackTimer = setTimeout(() => {
		window.location.href = webLink;
		clear();
	}, 900);

	document.addEventListener("visibilitychange", onVisibilityChange, { once: false });
	window.addEventListener("pagehide", onPageHide, { once: true });
	window.addEventListener("blur", onBlur, { once: true });

	// Try deep link (same tab)
	try {
		window.location.href = deepLink;
	} catch (e) {
		// If blocked, fallback will trigger
	}
};

export const bindTelegramLinks = (username) => {
	if (!username) {
		return;
	}
	document.addEventListener("click", (event) => {
		const target = event.target instanceof Element ? event.target : null;
		if (!target) {
			return;
		}
		const link = target.closest("a[href]");
		if (!link) {
			return;
		}
		const href = link.getAttribute("href") || "";
		// Match links pointing to this username on t.me
		if (/^https:\/\/t\.me\//.test(href) && href.includes(`/${username}`)) {
			event.preventDefault();
			let text = "";
			try {
				const url = new URL(href, window.location.origin);
				text = url.searchParams.get("text") || link.getAttribute("data-telegram-text") || "";
			} catch (_) {
				text = link.getAttribute("data-telegram-text") || "";
			}
			openTelegram(username, text);
		}
	}, { capture: true });
};
