export class Burger {
	constructor() {
		this.init();
	}

	init() {
		const toggle = document.getElementById("toggle");
		const header = document.querySelector("header");
		const mainNav = document.querySelector(".main-nav");

		if (!toggle || !header || !mainNav) {
			return;
		}

		const openMenu = () => {
			header.classList.add("header--openned");
			toggle.setAttribute("aria-pressed", "true");
		};

		const closeMenu = () => {
			header.classList.remove("header--openned");
			toggle.setAttribute("aria-pressed", "false");
		};

		const toggleMenu = () => {
			if (header.classList.contains("header--openned")) {
				closeMenu();
			} else {
				openMenu();
			}
		};

		// Toggle by button
		toggle.addEventListener("click", (event) => {
			event.stopPropagation();
			toggleMenu();
		});

		// Click outside closes menu
		document.addEventListener("click", (event) => {
			if (!header.classList.contains("header--openned")) {
				return;
			}
			const target = event.target;
			if (target instanceof Element) {
				if (!mainNav.contains(target)) {
					closeMenu();
				}
			}
		});

		// Esc closes menu
		document.addEventListener("keydown", (event) => {
			if (event.key === "Escape" && header.classList.contains("header--openned")) {
				closeMenu();
			}
		});

		// Click on navigation link closes menu (mobile UX)
		mainNav.addEventListener("click", (event) => {
			const target = event.target;
			if (target instanceof Element && target.closest(".main-nav__link")) {
				closeMenu();
			}
		});
	}
}